body {
  font-family: "Wix Madefor Display", sans-serif;
  background: radial-gradient(
    circle at 10% 20%,
    rgb(239, 246, 249) 0%,
    rgb(206, 239, 253) 90%
  );
}

h1 {
  color: lightskyblue;
  font-size: 35px;
  font-weight: 100;
  line-height: 28px;
  margin-bottom: 4px;
}

ul {
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
  margin: 0;
  padding: 0;
  color: #9aa0a6;
  font-size: 16px;
  font-weight: 100;
  line-height: 19px;
}

li::first-letter {
  text-transform: capitalize;
}

.weather-app-wrapper {
  max-width: 600px;
  margin: 30px auto;
}

.weather-app {
  border: 3px light;
  color: rgb(123, 103, 238);
  border-radius: 5px;
  padding: 20px;
  background: linear-gradient(to top, #dfe9f3 0%, white 100%);
  margin-bottom: 20px;
}

.weather-temperature img {
  margin-right: 10px;
  height: 64px;
  width: 64px;
}

.weather-temperature strong {
  color: rgb(33, 33, 33);
  font-size: 64px;
  font-weight: 400;
  line-height: 1;
}

.weather-temperature .units {
  position: relative;
  font-size: 16px;
}

.weather-forecast {
  margin-top: 20px;
  text-align: center;
  color: mediumslateblue;
}

.weather-forecast-date {
  font-size: 16px;
  opacity: 0.6;
}

.weather-forecast-temperature-min {
  opacity: 0.5;
}

.weather-forecast-temperatures {
  font-size: 13px;
}

.btn-primary {
  font-family: "Wix Madefor Display", sans-serif;
  color: white;
  background-color: lightskyblue;
  border-color: lightskyblue;
  display: block;
  text-align: center;
  border-radius: 10px;
  width: 110px;
  height: 40px;
}

.btn-primary:hover {
  cursor: pointer;
  background-color: white;
  color: lightskyblue;
  border: lightskyblue;
}
